.items {
  width: 350px;
}
.items p {
  margin: 6px 0;
}
.item-prices {
  display: flex;
  gap: 20px;
}
.item-prices .item-price-new {
  color: #374151;
  font-size: 18px;
  font-weight: 600;
}
.item-prices .item-price-old {
  color: #374151;
  font-size: 18px;
  font-weight: 500;
  text-decoration: line-through;
}
.items:hover {
  transform: scale(1.05);
  transition: all 0.6s ease;
}